@charset "utf-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

*{margin: 0; padding: 0;}
body{
  font-family: "Noto Sans", sans-serif;
  font-size: 13px;
  color: #333;
  background-color: #eff3f7;
}
a{
  color: #333;
  text-decoration: none;
  font-size: 16px;
}
ul li{
  list-style: none;
}

/* 공통서식 */

/* 상단 헤더 서식 */
.header{
  width: 200px;
  height: 100%;
  text-align: center;
  position: fixed;
  top: 0px; left: 0px;
  z-index: 100;

}
h1{
  padding: 70px 0px 50px 0px;
}
/* 네비게이션서식 */
.navi {
  padding-top: 70px;
  text-align: center;
}
.navi li{
  line-height: 40px;
}
.navi a{
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color: #5d5d5d;
  text-shadow: 0px 1px 1px #adadad;
}
.navi a:hover{
  text-decoration: underline;
}

main{
  width: calc(100% - 200px);
  height: 100%;
  position: fixed;
  left: 200px;
  top: 0px;
  padding-top: 50px;
  overflow: scroll;
}
/* 푸터서식 */
.footer{
  width: 100%;
  line-height: 40px;
  background-color: #333;
}
.footer address{
  color: #fff;
  text-align: center;
  font-size: normal;
}

